<template>
  <div class="hello">
     <div id="mapContainer" style="width: 100%; height: 300px; background: grey;" >
     </div>
     <button @click="locateMe">Get location</button>
      <div v-if="errorStr">
    Sorry, but the following error
    occurred: {{errorStr}}
  </div>
       <div v-if="location">
    Your location data is {{ location.Address.Label }}
  </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
     
     data: function() {
      return {
        map: null,
        maptypes: null,
        location:null,
        gettingLocation: false,
        errorStr:null
      } 
  },
     methods: {
       async getLocation() {
        const platform = new H.service.Platform({
      app_id: 'ckvfiN0Xc8cGjQ1JjfG3',
      app_code: 'ALyXNGKfgndS9D419jqC0g',
      useCIT: true,
      useHTTPS: true,
    });
           navigator.geolocation.getCurrentPosition(position => { 

     function addMarkersToMap(map) {
  var parisMarker = new H.map.Marker({lat:position.coords.latitude,lng:position.coords.longitude});
  map.addObject(parisMarker);
}
    
       var defaultLayers = platform.createDefaultLayers();
       var map = new H.Map(document.getElementById('mapContainer'),
                defaultLayers.normal.map,{
                center: {lat:position.coords.latitude,lng:position.coords.longitude},
                zoom: 15
                       })
       
           //Step 3: make the map interactive
        // MapEvents enables the event system
        // Behavior implements default interactions for pan/zoom (also on mobile touch environments)
        var behavior = new H.mapevents.Behavior(new H.mapevents.MapEvents(map));
        // Create the default UI components
        var ui = H.ui.UI.createDefault(map, defaultLayers);

        // Now use the map as required...
        addMarkersToMap(map);

        })

           const geocoder = platform.getGeocodingService();
      return new Promise((resolve, reject) => {

        if(!("geolocation" in navigator)) {
          reject(new Error('Geolocation is not available.'));
        }

        navigator.geolocation.getCurrentPosition(pos => {
          let reverseGeocodingParameters = {
            prox: `${pos.coords.latitude},${pos.coords.longitude}`,
            mode: 'retrieveAddresses',
            maxresults: 1
          };

          geocoder.reverseGeocode(
            reverseGeocodingParameters,
            res => {
              let results = res.Response.View;
              if(results.length === 0) {
                resolve('No match.')
              } else {
                resolve(results[0].Result[0].Location);
              }
            },
            e => reject(e) 
          );
        }, err => {
          reject(err);
        });

      });
         },
    async locateMe() {

      this.gettingLocation = true;
      try {
        this.gettingLocation = false;
        this.location = await this.getLocation();
      } catch(e) {
        this.gettingLocation = false;
        this.errorStr = e.message;
      }
      
    }
},

}
</script>
